import React from 'react'
import './App.css';
import Screen from './Screen';
import ZingTouch from 'zingtouch';
import Music1 from './music/Senorita.mp3';

class App extends React.Component {
  constructor() {
    super();
    this.state = {
      menu: ['Games', 'Music', 'Settings', 'Cover'],
      enter: 0,
      activeItem: 'Settings',
      activePage: 'Home',
      play: true,
      sound: Music1,
    }
  }

  rotateWheel = () => {
    var containerElement = document.getElementById('inner-container');
    var activeRegion = new ZingTouch.Region(containerElement);
    // var childElement = document.getElementById('inner-container');
    var change = 0;
    var self = this;
    self.state.enter = self.state.enter + 1;

    if (self.state.enter < 2) {
      activeRegion.bind(containerElement, 'rotate', function (event) {
        //Perform Operations
        var newAngle = event.detail.distanceFromLast;
        // console.log(newAngle);

        if (newAngle < 0) {
          // console.log(change);
          change++;
          if (change === 15) {
            console.log("change state");
            change = 0;
            if (self.state.activePage === 'Home') {
              if (self.state.activeItem === 'Games') {
                self.setState({
                  activeItem: "Music",
                })
              } else if (self.state.activeItem === 'Music') {
                self.setState({
                  activeItem: "Settings",
                })
              } else if (self.state.activeItem === 'Settings') {
                self.setState({
                  activeItem: "Cover",
                })
              } else if (self.state.activeItem === 'Cover') {
                self.setState({
                  activeItem: "Settings",
                })
              }
            } else if (self.state.activePage === 'Music') {
              if (self.state.activeItem === 'MyMusic') {
                self.setState({
                  activeItem: "Artists"
                })
              } else if (self.state.activeItem === 'Artists') {
                self.setState({
                  activeItem: 'MyMusic'
                })
              }
            } else if (self.state.activePage === 'Artists') {
              if (self.state.activeItem === 'Artist1') {
                self.setState({
                  activeItem: 'Artist2'
                })
              } else if (self.state.activeItem === 'Artist2') {
                self.setState({
                  activeItem: 'Artist3'
                })
              } else if (self.state.activeItem === 'Artist3') {
                self.setState({
                  activeItem: 'Artist4'
                })
              } else if (self.state.activeItem === 'Artist4') {
                self.setState({
                  activeItem: 'Artist1'
                })
              }
            }else if (self.state.activePage === 'Artist1') {
              if (self.state.activeItem === 'Song1') {
                self.setState({
                  activeItem: 'Song2'
                })
              } else if (self.state.activeItem === 'Song2') {
                self.setState({
                  activeItem: 'Song3'
                })
              } else if (self.state.activeItem === 'Song3') {
                self.setState({
                  activeItem: 'Song4'
                })
              } else if (self.state.activeItem === 'Song4') {
                self.setState({
                  activeItem: 'Song1'
                })
              }
            } else if (self.state.activePage === 'Cover') {
              if (self.state.activeItem === 'Cover1') {
                self.setState({
                  activeItem: 'Cover2'
                })
              } else if (self.state.activeItem === 'Cover2') {
                self.setState({
                  activeItem: 'Cover3'
                })
              } else if (self.state.activeItem === 'Cover3') {
                self.setState({
                  activeItem: 'Cover4'
                })
              } else if (self.state.activeItem === 'Cover4') {
                self.setState({
                  activeItem: 'Cover1'
                })
              }
            }
          }
        } else {
          // console.log(change);
          change++;
          if (change === 15) {
            console.log("change state");
            change = 0;
            if (self.state.activePage === 'Home') {
              if (self.state.activeItem === 'Games') {
                self.setState({
                  activeItem: "Cover"
                })
              } else if (self.state.activeItem === 'Music') {
                self.setState({
                  activeItem: "Games"
                })
              } else if (self.state.activeItem === 'Settings') {
                self.setState({
                  activeItem: "Music"
                })
              } else if (self.state.activeItem === 'Cover') {
                self.setState({
                  activeItem: "Settings"
                })
              }
            } else if (self.state.activePage === 'Music') {
              if (self.state.activeItem === 'MyMusic') {
                self.setState({
                  activeItem: "Artists"
                })
              } else if (self.state.activeItem === 'Artists') {
                self.setState({
                  activeItem: 'MyMusic'
                })
              }
            } 
            else if (self.state.activePage === 'Artists') {
              if (self.state.activeItem === 'Artist1') {
                self.setState({
                  activeItem: 'Artist2'
                })
              } else if (self.state.activeItem === 'Artist2') {
                self.setState({
                  activeItem: 'Artist3'
                })
              } else if (self.state.activeItem === 'Artist3') {
                self.setState({
                  activeItem: 'Artist4'
                })
              } else if (self.state.activeItem === 'Artist4') {
                self.setState({
                  activeItem: 'Artist1'
                })
              }
            } else if (self.state.activePage === 'Artist1') {
              if (self.state.activeItem === 'Song1') {
                self.setState({
                  activeItem: 'Song2'
                })
              } else if (self.state.activeItem === 'Song2') {
                self.setState({
                  activeItem: 'Song3'
                })
              } else if (self.state.activeItem === 'Song3') {
                self.setState({
                  activeItem: 'Song4'
                })
              } else if (self.state.activeItem === 'Song4') {
                self.setState({
                  activeItem: 'Song1'
                })
              }
            } else if (self.state.activePage === 'Cover') {
              if (self.state.activeItem === 'Cover1') {
                self.setState({
                  activeItem: 'Cover2'
                })
              } else if (self.state.activeItem === 'Cover2') {
                self.setState({
                  activeItem: 'Cover3'
                })
              } else if (self.state.activeItem === 'Cover3') {
                self.setState({
                  activeItem: 'Cover4'
                })
              } else if (self.state.activeItem === 'Cover4') {
                self.setState({
                  activeItem: 'Cover1'
                })
              }
            }
          }
        }
      });
    } else {
      console.log("Not allowed to enter")
    }

  }

  changePage = () => {
    if (this.state.activeItem === 'Music') {
      this.setState({
        activeItem: 'MyMusic',
        activePage: this.state.activeItem
      })
    } else if (this.state.activeItem === 'Cover') {
      this.setState({
        activeItem: 'Cover1',
        activePage: this.state.activeItem
      })
    } else if (this.state.activeItem === 'Artists') {
      this.setState({
        activeItem: 'Artist1',
        activePage: this.state.activeItem
      })
    } else if (this.state.activeItem === 'Artist1') {
      this.setState({
        activeItem: 'Song1',
        activePage: this.state.activeItem
      })
    }else {
      this.setState({
        activeItem: this.state.activeItem,
        activePage: this.state.activeItem,
      })
    }
    console.log(this.state.activePage, this.state.activeItem)
  }

  changePageToHomeScreen = () => {
    if (this.state.activeItem === 'MyMusic' || this.state.activeItem === 'Artists') {
      this.setState({
        activeItem: 'Music',
        activePage: 'Home'
      })
    } else if (this.state.activeItem === 'Cover1' || this.state.activeItem === 'Cover2' || this.state.activeItem === 'Cover3' || this.state.activeItem === 'Cover4') {
      this.setState({
        activeItem: 'Cover',
        activePage: 'Home'
      })
    } else if (this.state.activeItem === 'Artist1' || this.state.activeItem === 'Artist2' || this.state.activeItem === 'Artist3' || this.state.activeItem === 'Artist4') {
      this.setState({
        activeItem: 'Artists',
        activePage: 'Music'
      })
    } else if (this.state.activeItem === 'Song1' || this.state.activeItem === 'Song2' || this.state.activeItem === 'Song3' || this.state.activeItem === 'Song4') {
      this.setState({
        activeItem: 'Artist1',
        activePage: 'Artists'
      })
    } else {
      this.setState({
        activeItem: this.state.activeItem,
        activePage: 'Home'
      })
    }

  }

  toggle = () => {
    if (this.state.activePage === 'MyMusic') {
      if (this.state.play === true) {
        this.state.audio.pause();
        this.setState({
          play: false
        })
      } else {
        this.state.audio.play();
        this.setState({
          play: true
        })
      }
      console.log("toggled")
    }
  }

  componentDidMount() {
    var today = new Date(),
      time = today.getHours() + ':' + today.getMinutes() + ':' + today.getSeconds();
    let audio = document.getElementById("audio-element");
    console.log(audio, time)
    this.setState({
      audio: audio,
      currentTime: time
    })
    console.log(this.state)
  }
  render() {
    return (
      <div className="App">
            <audio id="audio-element">
              <source src={this.state.sound}></source>
            </audio>
        <Screen
          activeItem={this.state.activeItem}
          activePage={this.state.activePage}
          optionsInMenu={this.state.menu}
          musicPlay={this.state.play}
          currentTime={this.state.currentTime}
          audio={this.state.audio}
        />
        <div id='inner-container' className="wheel" onMouseOver={this.rotateWheel}>
          <div className="buttonContainer" >
            <div className="menuButton">
              <i className="image fa-solid fa-bars" onClick={this.changePageToHomeScreen}></i>
            </div>
          </div>

          <div className="buttonContainer" >
            <div className="middleButtons" >
              <i className="image fa-solid fa-backward-fast"></i>
              <div onClick={this.changePage} style={{ backgroundImage: 'linear-gradient(45deg, #8c8181, transparent)', width: '5rem', height: '5rem', borderRadius: '50%' }}></div>
              <i className="image fa-solid fa-forward-fast" ></i>
            </div>
          </div>

          <div className="buttonContainer">
            <div onClick={this.toggle} className="playButton" >
              <i onClick={this.toggle} className="image fa-solid fa-play" ></i>&nbsp;<i onClick={this.toggle} className="image fa-solid fa-pause" ></i>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default App;
