import React from 'react';

class Settings extends React.Component{
    
    render(){
        return(
            <div style={styles.settings} >
                <div style={styles.titleBar}>
                        <p style={styles.ipodBar}>iPod</p>
                        <div style={styles.timeBar}>{this.props.currentTime}
                        <img style={styles.battery} alt="background" src="https://cdn-icons-png.flaticon.com/512/3103/3103446.png"></img>
                        </div>
                </div>
                <div style={{width : '50%' , height : '50%',display:'flex' , flexDirection : 'row' , justifyContent: 'center'}}>
                    <img style={styles.image} alt="settings" src="https://cdn-icons-png.flaticon.com/512/3132/3132084.png" />
                </div>
            </div>
        );
    }
    
}

const styles = {
    settings : {
        width : '100%',
        height : '100%',
        display : 'flex',
        flexDirection : 'row',
        justifyContent : 'center',
        flexWrap : 'wrap'
    },
    image : {
        width:'100%',
        height:'100%',
        alignSelf : 'center'
    },
    ipodBar : {
        fontSize: '1.25rem',
        fontWeight:'bold', 
        paddingLeft: '5%',
    },
    timeBar : {
        width: '30%',
        padding: '0.2rem',
        fontSize: '1rem',
        color: 'black',
        display: 'flex',
        flexDirection: 'row',
    },
    titleBar : {
        height:'22%',
        width:'100%',
        borderRadius:'12px 0 0 0',
        backgroundImage: 'linear-gradient(0deg, rgb(123, 132, 140), transparent)',
        borderBottom: '1px solid #6c757d',
        padding : '1px 5px 10px 8px',
        display:'flex',
        flexDirecton : 'row',
        justifyContent : 'space-between'

    },
    battery :{
        width : '20px',
        height: '20px',
        marginLeft: '5%',
        padding: '2px',
    }
}

export default Settings;