import React from "react";
import Games from './Games'
import Home from "./Home";
import Music from './Music';
import Settings from "./Settings";
import CoverMenu from './CoverMenu'
import MyMusic from './MyMusic'
import Artists from "./Artists";
import Artist1 from "./Artist1";
import Cover1 from './cover/Cover1'
import Cover2 from './cover/Cover2'
import Cover3 from './cover/Cover3'
import Cover4 from './cover/Cover4'

class Screen extends React.Component {
    render() {
        console.log(this.props.activePage, this.props.activeItem, this.props.currentTime)
        return (
            <div style={styles.screen} id='screen-container'>
                {this.props.activePage==='Home'?<Home currentTime={this.props.currentTime} optionsInMenu={this.props.optionsInMenu} activeItem={this.props.activeItem}/> : null}
                {this.props.activePage==='Games'?<Games currentTime={this.props.currentTime} /> : null}
                {this.props.activePage==='Music'?<Music currentTime={this.props.currentTime} activePage={this.props.activePage} activeItem={this.props.activeItem}/> : null}
                {this.props.activePage==='Settings'?<Settings currentTime={this.props.currentTime} /> : null}
                {this.props.activePage==='Cover'?<CoverMenu currentTime={this.props.currentTime} activePage={this.props.activePage} activeItem={this.props.activeItem}/> : null}
                {this.props.activePage==='MyMusic'?<MyMusic currentTime={this.props.currentTime} audio={this.props.audio} /> : null}
                {this.props.activePage==='Artists'?<Artists currentTime={this.props.currentTime} activePage={this.props.activePage} activeItem={this.props.activeItem}/> : null}
                {this.props.activePage==='Artist1'?<Artist1 currentTime={this.props.currentTime} activePage={this.props.activePage} activeItem={this.props.activeItem} /> : null}
                {this.props.activePage==='Cover1'?<Cover1 currentTime={this.props.currentTime} /> : null}
                {this.props.activePage==='Cover2'?<Cover2 currentTime={this.props.currentTime} /> : null}
                {this.props.activePage==='Cover3'?<Cover3 currentTime={this.props.currentTime} /> : null}
                {this.props.activePage==='Cover4'?<Cover4 currentTime={this.props.currentTime} /> : null}
                
            </div>
        );
    }
}

const styles = {
    screen : {
        height : '50%',
        width : '95%',
        borderRadius : '12px',
        backgroundColor : 'white',
        margin: '2% 2.5%'
    }
}

export default Screen;