import React from 'react';

class Music extends React.Component{

    render(){
        console.log(this.props.activePage, this.props.activeItem)
        return(
            <div style={styles.musicScreen} id='music-screen'>
                <div style={styles.menu} id='menu-list'>
                    <div style={styles.titleBar}>
                            <p style={styles.ipodBar}>iPod</p>
                            <div style={styles.timeBar}>{this.props.currentTime}
                        <img style={styles.battery} alt="background" src="https://cdn-icons-png.flaticon.com/512/3103/3103446.png"></img>
                        </div>
                    </div>
                    <div >
                        <li style={styles.list} className={this.props.activeItem === 'MyMusic' ? 'active' : ''}>My Music{this.props.activeItem === 'MyMusic' ? <span style={{ float: 'right', fontWeight: 'bold' }}><i className="fa-solid fa-caret-right"></i></span> : ''}</li>
                        <li style={styles.list} className={this.props.activeItem === 'Artists' ? 'active' : ''}>Artists{this.props.activeItem === 'Artists' ? <span style={{ float: 'right', fontWeight: 'bold' }}><i className="fa-solid fa-caret-right"></i></span> : ''}</li>
                    </div>
                </div>
                <div style={styles.imageContainer} id='image-container'>
                </div>
            </div>
        );
    }
    
}

const styles = {
    musicScreen : {
        height : '100%',
        width : '100%',
        display : 'flex',
        flexDirecton : 'row'
    },
    menu : {
        //border:'1px solid black',
        height : '100%',
        width : '50%',
        boxShadow: '10px 0px 15px -5px rgba(0,0,0,0.75)',
        zIndex : '1'
    },
    imageContainer : {
        //border:'1px solid black',
        height : '100%',
        width : '50%',
        backgroundImage : 'url("https://i.pinimg.com/originals/b5/ca/9a/b5ca9a6c64b6a5c445dc993dd6294d10.jpg")',
        backgroundSize : 'cover',
        backgroundPosition : 'center',
        borderRadius : '0 12px 12px 0'
    },
    ipodBar : {
        fontSize: '1.25rem',
        fontWeight:'bold', 
        paddingLeft: '10%',
    },
    timeBar : {
        width: '55%',
        padding: '0.2rem',
        fontSize: '1rem',
        color: 'black',
        display: 'flex',
        flexDirection: 'row',
    },
    titleBar : {
        height:'22%',
        // width:'100%',
        borderRadius:'12px 0 0 0',
        backgroundImage: 'linear-gradient(0deg, rgb(123, 132, 140), transparent)',
        borderBottom: '1px solid #6c757d',
        padding : '1px 5px 10px 8px',
        display:'flex',
        flexDirecton : 'row',
        justifyContent : 'space-between'

    },
    battery :{
        width : '20px',
        height: '20px',
        marginLeft: '5%',
        padding: '2px',
    },
    list : {
        position: 'relative',
        display: 'block',
        padding: '0.75rem 1rem',
        color: 'grey',
        textDecoration: 'none',
        backgroundColor: '#fff',
        border: '1px solid rgba(0, 0, 0, 0.125)',
        boxShadow: '10px 0px 15px -5px rgba(0,0,0,0.75)',
    }
}


export default Music;