import React from 'react';

class Home extends React.Component {
    render() {
        const { optionsInMenu } = this.props;
        return (
            <div style={styles.home} className="home">
                <div style={styles.menu} className="menu">
                    <div style={styles.titleBar} className="titleBar">
                        <p style={styles.ipodBar}>iPod</p>
                        <div style={styles.timeBar}>{this.props.currentTime}
                        <img style={styles.battery} alt="background" src="https://cdn-icons-png.flaticon.com/512/3103/3103446.png"></img>
                        </div>
                        
                    </div>
                    <div style={styles.menulist} className="menuList">
                        <li style={styles.list} className={this.props.activeItem === 'Games' ? 'active' : ''}>{optionsInMenu[0]}{this.props.activeItem === 'Games' ? <span style={{ float: 'right', fontWeight: 'bold' }}><i className="fa-solid fa-caret-right"></i></span> : ''}</li>
                        <li style={styles.list} className={this.props.activeItem === 'Music' ? 'active' : ''} >{optionsInMenu[1]}{this.props.activeItem === 'Music' ? <span style={{ float: 'right', fontWeight: 'bold' }}><i className="fa-solid fa-caret-right"></i></span> : ''}</li>
                        <li style={styles.list} className={this.props.activeItem === 'Settings' ? 'active' : ''} >{optionsInMenu[2]}{this.props.activeItem === 'Settings' ? <span style={{ float: 'right', fontWeight: 'bold' }}><i className="fa-solid fa-caret-right"></i></span> : ''}</li>
                        <li style={styles.list1} className={this.props.activeItem === 'Cover' ? 'active' : ''} >{optionsInMenu[3]}{this.props.activeItem === 'Cover' ? <span style={{ float: 'right', fontWeight: 'bold' }}><i className="fa-solid fa-caret-right"></i></span> : ''}</li>
                    </div>
                </div>
            </div>
        )
    }
}

const styles = {
    home: {
        // marginTop: '10%',
        // marginLeft:'25%',
        // height: '100%',
        width: '100%',
        backgroundImage : 'url("https://i.pinimg.com/originals/b5/ca/9a/b5ca9a6c64b6a5c445dc993dd6294d10.jpg")',
        backgroundSize : 'cover',
        backgroundPosition : 'center',
        borderRadius : '12px 12px 12px 12px'
    },
    ipodBar : {
        fontSize: '1.25rem',
        fontWeight:'bold', 
        paddingLeft: '10%',
    },
    timeBar : {
        width: '55%',
        padding: '0.2rem',
        fontSize: '1rem',
        color: 'black',
        display: 'flex',
        flexDirection: 'row',
    },
    menu : {
        //border:'1px solid black',
        height : '100%',
        width : '50%',
        boxShadow: '10px 0px 15px -5px rgba(0,0,0,0.75)',
        zIndex : '1'
    },
    titleBar : {
        height:'22%',
        // width:'100%',
        borderRadius:'12px 0 0 0',
        backgroundImage: 'linear-gradient(0deg, rgb(123, 132, 140), transparent)',
        borderBottom: '1px solid #6c757d',
        // padding : '1px 5px 10px 8px',
        display:'flex',
        flexDirecton : 'row',
        justifyContent : 'space-between'
    },
    battery :{
        width : '20px',
        height: '20px',
        marginLeft: '5%',
        padding: '2px',
    },
    list : {
        position: 'relative',
        display: 'block',
        padding: '0.75rem 1rem',
        color: 'grey',
        textDecoration: 'none',
        backgroundColor: '#fff',
        border: '1px solid rgba(0, 0, 0, 0.125)',
        boxShadow: '10px 0px 15px -5px rgba(0,0,0,0.75)',
    },
    list1 : {
        position: 'relative',
        display: 'block',
        padding: '0.75rem 1rem',
        color: 'grey',
        textDecoration: 'none',
        backgroundColor: '#fff',
        border: '1px solid rgba(0, 0, 0, 0.125)',
        boxShadow: '10px 0px 15px -5px rgba(0,0,0,0.75)',
        borderRadius : '0px 0px 0px 12px'
    }
}
export default Home;